.about {
  padding: 37px 0 60px 0;

  @media (min-width: @tablet-width) {
    padding: 44px 0 82px 0;
  }

  @media (min-width: @desktop-width) {
    padding-top: 70px;
    padding-bottom: 88px;
  }
}

.about__title {
  font-size: 25px;
  line-height: 30px;
  color: @color-dark;
  margin-top: 0;
  margin-bottom: 28px;

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 39px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.about__list {
  font-size: 17px;
  font-weight: 700;
  line-height: 1;
  color: @color-dark;
  .normalize-list();

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    grid-row-gap: 57px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 40% 60%;
  }
}

.about__item {
  &--materials {
    background-image: url("../img/sprite.svg#materials");
    background-repeat: no-repeat;
    background-size: 33px 38px;
    padding-top: 54px;
    padding-bottom: 30px;
  }

  &--style {
    background-image: url("../img/sprite.svg#style");
    background-repeat: no-repeat;
    background-size: 47px 27px;
    padding-top: 53px;
    padding-bottom: 35px;
  }

  &--likes {
    background-image: url("../img/sprite.svg#likes");
    background-repeat: no-repeat;
    background-size: 34px 27px;
    padding-top: 48px;
    padding-bottom: 32px;
  }

  &--handmade {
    background-image: url("../img/sprite.svg#handmade");
    background-repeat: no-repeat;
    background-size: 35px 34px;
    padding-top: 51px;
    padding-bottom: 33px;
  }

  &--support {
    background-image: url("../img/sprite.svg#support");
    background-repeat: no-repeat;
    background-size: 55px 23px;
    padding-top: 49px;
    padding-bottom: 32px;
  }

  &--gift {
    background-image: url("../img/sprite.svg#gift");
    background-repeat: no-repeat;
    background-size: 33px 32px;
    padding-top: 52px;
  }

  @media (max-width: @mobile-width-only) {
    &--materials {
      background-position: 7px 0;
    }

    &--likes {
      background-position: 7px 0;
    }

    &--handmade {
      background-position: 3px 0;
    }

    &--support {
      background-position: -3px 0;
    }

    &--gift {
      background-position: 3px 0;
    }
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 0;

    &--materials {
      padding-top: 58px;
      background-position: 7px 0;
    }

    &--handmade {
      padding-top: 58px;
      background-position: 3px 4px;
    }

    &--style {
      order: 1;
    }

    &--likes {
      order: 3;
      padding-top: 54px;
      background-position: 7px 3px;
    }

    &--support {
      order: 2;
      padding-top: 54px;
      background-position: -3px 2px;
    }

    &--gift {
      order: 3;
      padding-top: 54px;
      background-position: 2px 0;
    }
  }
}

.about__description {
  margin: 0;
  line-height: 20px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 22px;
  }
}

.about__wrapper {
  @media (min-width: @desktop-width) {
    padding-left: 175px;
  }
}
